* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #afc7fd;
}

h1 {
  text-align: center;
  margin-top: 20px;
}

.Todoform {
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  max-width: 600px;
}

.Todoform input[type="text"] {
  padding: 10px;
  width: 70%;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  font-size: 1rem;
}

.Todoform .todo-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #3867d6;
  color: #fff;
  font-size: 1.2rem;
  cursor: pointer;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px auto;
  max-width: 600px;
  padding: 10px;
  border: 1px solid #706fd3;
  border-radius: 5px;
  background-color: #474787;
  color: #fff;
}

.Todo input[type="checkbox"] {
  margin-right: 10px;
}

.Todo span {
  flex: 1;
}

.Todo button {
  border: none;
  background-color: transparent;
  font-size: 16px;
  cursor: pointer;
}

.Todo .delete-btn {
  color: #eb3b5a;
  margin-right: 10px;
}

.Todo .edit-btn {
  color: #20bf6b;
}


.form-submit {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  max-width: 600px;
}

.form-submit input[type="text"] {
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  width: 70%;
  font-size: 16px;
}

.form-submit button[type="submit"],
.form-submit button[type="button"] {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin: 10px;
}

.form-submit .save-btn {
  background-color: #4CAF50;
  color: white;
}

.form-submit .cancel-btn {
  background-color: #f44336;
  color: white;
}